@import "./variables.scss";

.cart {
  z-index: 1000;
  &__header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;

    h2 {
      font-weight: 800;
      font-size: 30px;
      line-height: 34px;
    }
  }

  &__container {
    position: absolute;
    width: 325px;
    height: 744px;
    right: -60px;
    top: -17px;
    box-sizing: border-box;
    padding: 36px 32px;
    z-index: 10000;
    background: $white;
    box-shadow: 0px 4px 46px $semiBlack;
    border-radius: 30px 0px 0px 30px;
  }

  &__button {
    cursor: pointer;
    border: none;
    width: 46px;
    height: 46px;
    background: $lightGray;
    border-radius: 15px;

    img {
      display: block;
      margin: 19.5px auto;
    }
  }
  &--item {
    &__quantity {
      display: flex;
      align-items: center;

      span {
        margin: auto;
      }
    }
    color: black;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    &:not(:last-child) {
      margin-bottom: 5px;
    }
    p {
      font-weight: 700;
      font-size: 8px;
      line-height: 18px;
    }
    &__details {
      h2 {
        text-align: left;
        font-weight: 700;
        font-size: 12px;
        line-height: 18px;
        margin: 0;
      }
      p {
        text-align: left;
        max-width: 90px;
        font-weight: 600;
        font-size: 7px;
        line-height: 10px;
        color: $gray;
        margin: 0;
      }
    }

    &__image {
      margin-right: 5px;
      width: 47px;
      height: 47px;
      border-radius: 15px;
      background-color: $lightYellow2;
      box-sizing: border-box;
      padding: 5px;

      img {
        width: 36px;
        height: 36px;
        display: block;
        margin: auto;
      }
    }
    &__price {
      font-weight: 700;
      font-size: 8px;
      line-height: 18px;
      display: flex;
      flex-direction: column;
    }
  }

  &__order {
    &--button {
      position: absolute;
      bottom: 20px;
      border: none;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 252px;
      height: 50px;
      background: $blue;
      border-radius: 15px;
      box-sizing: border-box;
      padding: 16px;
      p {
        font-weight: 700;
        font-size: 13px;
        line-height: 18px;
        color: $white;
      }
      &--placed {
        position: absolute;
        bottom: 353px;
      }
    }
    &--list {
      height: 553px;
      overflow: auto;
      -ms-overflow-style: none;

      &::-webkit-scrollbar {
        display: none;
      }
    }
  }
  &--placed {
    position: absolute;
    top: 133px;
    left: 55px;
    p {
      font-weight: 800;
      font-size: 14px;
      line-height: 16px;
      text-align: center;
    }
    &--not {
      font-weight: 800;
      font-size: 14px;
      line-height: 16px;
      text-align: center;
    }
    img {
      display: block;
      margin: auto;
    }
  }
  &--buttons {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &--empty {
    font-weight: 800;
    font-size: 14px;
    line-height: 16px;
    text-align: center;
  }
}

.button {
  &--order {
    border: none;
    background-color: $darkWhite;
    border-radius: 5px;
    font-weight: 700;
    font-size: 12px;
    line-height: 18px;
    box-sizing: border-box;
  }
}
